<template>
  <div class="vux-tab-item" data-index="{{index}}" :class="{'vux-tab-selected':selected}" :style="{borderWidth:$parent.lineWidth+'px',borderColor:$parent.activeColor,color:selected ? $parent.activeColor : $parent.defaultColor, 'border': $parent.animate ? 'none' : 'auto'}" @click="tabClick()">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    props: {
      selected: {
        type: Boolean,
        required: false,
        default: false
      },
      index: {
        type: Number,
        required: true
      }
    },
    methods: {
      tabClick: function () {
        this.$dispatch('index:change', this.$el.getAttribute('data-index'))
      }
    }
  }
</script>



